import React, { Component } from 'react';

import {
    BrowserRouter as Router,
    Route
} from 'react-router-dom';
import Home from './components/Home';
import Voyages from './components/Voyages';
import Connexion from './components/Connexion';
import Inscription from './components/Inscription';
import 'w3-css/w3.css';

class App extends Component {
    render() {
        return (
		<Router>
		<div>
			<Route exact path="/2019_02_12_jsp4/" component={ Home } />
			<Route path="/2019_02_12_jsp4/voyages" component={ Voyages } />
			<Route path="/2019_02_12_jsp4/connexion" component={ Connexion } />
			<Route path="/2019_02_12_jsp4/inscription" component={ Inscription } />
			<Route component={ Home } />
		</div>
		</Router>
        );
    };
}

export default App;
